.age-calculator {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 100vh;
}

#dob {
  width: 300px;
  height: 30px;
  border-radius: 5px;
  border: 1px solid #ccc;
  padding: 5px;
  margin-bottom: 10px;
}

.calculate-button {
  background-color: blue;
  color: white;
  width: 150px;
  height: 50px;
  border-radius: 5px;
  border: none;
  margin-top: 20px;
}


